body {
  margin: 0;
  font-family: 'Avenir', sans-serif;
}

.nav-container {
  background: -webkit-linear-gradient(left, rgb(108, 127, 183), rgb(203, 211, 237));
  background: linear-gradient(to right, rgb(108, 127, 183), rgb(203, 211, 237));
}

.nav-container a {
  color: #FFE5D5;
}

.navigation {
	position: fixed;
}

.App {
  text-align: center;
}

.center {
  justify-content: center;
}

button {
  background-color: transparent;
  color: #FFE5D5;
}

.particles {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
  background-color: #8d9cca;
  background-image: url(/static/media/fiabwxoz.e193fe63.svg);
  background-repeat: no-repeat;
  background-size: cover;
}

.card {
  position: absolute;
  background-color: transparent;
  color: #FFE5D5;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  margin-bottom: 50%;
  padding: 1em;
  word-spacing: 10%;
  text-align: center;
}


.about-card {
	background-color: transparent;
	color: #FFE5D5;
	position: absolute;
	top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.about-card h1 {
	font-size: 1.4em;
}

.about-card p {
	font-size: 1em;
	font-weight: 500;
	color: #FFE5D5;
}
.project-card {
	background-color: transparent;
	display: inline-block;
}

.project-card h1 {
	color: #FFE5D5;
}
.contactIcon {
	fill: #FFE5D5;
}

.emailButton {
	background: transparent;
	padding: .2em;
	border-radius: 10%;
	color: #FFE5D5;
	font-size: 1em;
	font-weight: 600;
}

.emailButton:hover {
	opacity: 0.6;
}
